<mku-example>
        
        <template slot="source"><element-demo3 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div class=&quot;doc-form-container&quot;&gt;
    &lt;mku-form
      ref=&quot;form&quot;
      :model=&quot;formData&quot;
      :rules=&quot;rules&quot;
      :label-width=&quot;140&quot;
    &gt;
      &lt;mku-form-item prop=&quot;passwodOld&quot; label=&quot;旧密码：&quot;&gt;
        &lt;mku-input
          type=&quot;text&quot;
          placeholder=&quot;请输入旧的密码&quot;
          v-model=&quot;formData.passwodOld&quot;
        /&gt;
      &lt;/mku-form-item&gt;
      &lt;mku-form-item prop=&quot;passwordNew&quot; label=&quot;新密码：&quot;&gt;
        &lt;mku-input
          type=&quot;password&quot;
          placeholder=&quot;请设置新的密码&quot;
          v-model=&quot;formData.passwordNew&quot;
        /&gt;
      &lt;/mku-form-item&gt;
      &lt;mku-form-item prop=&quot;passwordNewAgain&quot; label=&quot;确认新密码：&quot;&gt;
        &lt;mku-input
          type=&quot;password&quot;
          placeholder=&quot;请再次确认新的密码&quot;
          v-model=&quot;formData.passwordNewAgain&quot;
        /&gt;
      &lt;/mku-form-item&gt;
      &lt;mku-form-item&gt;
        &lt;mku-button type=&quot;primary&quot; block @click=&quot;handleSubmit&quot;&gt;确认修改&lt;/mku-button&gt;
      &lt;/mku-form-item&gt;
    &lt;/mku-form&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      const validatePassword = (rule, value, callback) =&gt; {
        if (!value) {
          return callback(new Error('密码不能为空'))
        }
        if (value.length &lt; 6 || value.length &gt; 20) {
          return callback(new Error('密码长度在6-20位'))
        }
        callback()
      }
      const validatePasswordAgain = (rule, value, callback) =&gt; {
        if (value !== this.formData.passwordNew) {
          return callback(new Error('两次密码不一致，请重新输入'))
        }
        callback()
      }
      return {
        formData: {
          passwodOld: '',
          passwordNew: '',
          passwordNewAgain: ''
        },
        rules: {
          passwodOld: [
            { required: true, validator: validatePassword }
          ],
          passwordNew: [
            { required: true, validator: validatePassword },
          ],
          passwordNewAgain: [
            { required: true, validator: validatePassword },
            { validator: validatePasswordAgain },
          ]
        }
      }
    },
    methods: {
      handleSubmit () {
        this.$refs.form.validate(res =&gt; {
          if (!res) return
          // 提交表单
          // ...
        })
      },
      handleReset () {
        this.$refs.form.resetFields()
      }
    }
  }
&lt;/script&gt;
</code></pre></template></mku-example><h3>Form Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">model</td>
