<td style="text-align:left"><code>Object</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>{}</code></td>
</tr>
<tr>
<td style="text-align:left">rules</td>
<td style="text-align:left">表单的验证规则，支持所有 <a href="https://github.com/yiminghe/async-validator">async-validator</a> 规则</td>
<td style="text-align:left"><code>Object</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>{}</code></td>
</tr>
<tr>
<td style="text-align:left">label-width</td>
<td style="text-align:left">表单域标签的宽度，所有form-item都会继承</td>
<td style="text-align:left"><code>Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">show-message</td>
<td style="text-align:left">石佛显示表单的校验错误提示信息，所有form-item都会继承</td>
<td style="text-align:left"><code>Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">true</td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<div class="mku-doc__warning">
<p>强烈建议<code>rules</code>对象中的字段需要和<code>model</code>对象中的字段对应</p>
</div>
<h3>Form Methods</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调函数参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">validate</td>
<td style="text-align:left">对整个表单进行验证</td>
<td style="text-align:left">第一参数是验证完成的回调函数，该函数会回填一个<code>Boolean</code>参数表示是否通过验证;<br>第二个参数为验证发生错误的回调函数，会回填一个<code>error</code>对象（注意：不是验证失败，是发生错误）</td>
</tr>
<tr>
<td style="text-align:left">validateField</td>
<td style="text-align:left">对表单部分字段进行校验</td>
<td style="text-align:left">第一参数是需要校验的字段<code>（String|Array&lt;string&gt;）</code><br>第二个参数和第三个参数同<code>validate</code>方法的参数</td>
</tr>
<tr>
<td style="text-align:left">resetFields</td>
<td style="text-align:left">移除表单的验证结果，并将数据恢复到初始状态</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
