<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">prop</td>
<td style="text-align:left">对应model中的字段，用于指定验证和重置等方法时的字段</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">标签的文本内容</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">labelWidth</td>
<td style="text-align:left">标签的宽度，优先级高于<code>Form</code>的该属性</td>
<td style="text-align:left"><code>Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">labelPosition</td>
<td style="text-align:left">标签的位置，优先级高于<code>Form</code>的该属性</td>
<td style="text-align:left"><code>String</code></td>
<td style="text-align:left"><code>left|top|right</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">showMessage</td>
